<template>
  <b-card class="hp-project-table-card hp-analytics-project-table-card mb-5">
    <b-row align-v="center" align-h="between">
      <b-col cols="12" class="mb-32">
        <b-row align-v="center" align-h="between">
          <b-col cols="12" md="6">
            <h3>Latest Binance Id</h3>
            <p class="hp-p1-body mb-0">You can see latest binance id registered at here</p>
          </b-col>
          <div class="hp-header-search-item position-relative">
            <b-form-input placeholder="Search binance here..." autocomplete="off" v-model="email" type="email" @keyup.enter="findUser" />
          </div>
          <div>
            <div v-if="newID"></div>
            <div v-else>
              <b-button variant="primary-2" class="mr-5" @click="newID = true">
                <i class="ri-2x ri-chat-new-fill" style="font-size: 24px" />
                new binance id
              </b-button>
            </div>
          </div>
        </b-row>
      </b-col>
      <b-col>
        <b-alert variant="danger" v-if="errorfind" show>binance id must be valid number</b-alert>
      </b-col>
    </b-row>
    <div v-if="newID">
      <b-col>
        <b-form class="mb-3">
          <b-form-group id="input-group-2" label="input your binance id" label-for="binanceid2">
            <b-form-input id="binanceid" placeholder="Amount to Adjust" type="number" v-model="binance_id" style="max-width: 240px"></b-form-input>
          </b-form-group>
        </b-form>
      </b-col>
      <b-col>
        <h4 class="mt-5">Are You Sure to submit new binance id??</h4>
        <b-button variant="primary-2" class="mr-5" @click="addId(binance_id)">
          <i class="ri-2x ri-checkbox-fill" style="font-size: 24px" />
          confirm
        </b-button>
        <b-button variant="danger" class="mr-5" @click="newID = false">
          <i class="ri-2x ri-file-forbid-fill" style="font-size: 24px" />
          cancel
        </b-button>
      </b-col>
    </div>
    <div v-else>
      <div v-if="userData.list_data.length === 0">No Data To Display</div>
      <div v-else>
        <b-table-simple responsive borderless class="mb-0">
          <b-thead>
            <b-tr>
              <b-th>
                <span class="hp-badge-size font-weight-normal text-black-60 hp-text-color-dark-50 text-uppercase"> date </span>
              </b-th>
              <b-th>
                <span class="hp-badge-size font-weight-normal text-black-60 hp-text-color-dark-50 text-uppercase"> binance pay id </span>
              </b-th>
              <b-th>
                <span class="hp-badge-size font-weight-normal text-black-60 hp-text-color-dark-50 text-uppercase"> Country code </span>
              </b-th>
              <b-th>
                <span class="hp-badge-size font-weight-normal text-black-60 hp-text-color-dark-50 text-uppercase"> claimed </span>
              </b-th>
            </b-tr>
          </b-thead>
          <b-tbody>
            <b-tr :key="index" v-for="(item, index) in userData.list_data">
              <b-td>
                <span class="mb-0 text-black-80 hp-text-color-dark-30">
                  {{ new Date(item.created_at).toLocaleString('id-ID', { timeZone: 'Asia/Jakarta' }) }}
                </span>
              </b-td>
              <b-td>
                <span class="mb-0 text-black-80 hp-text-color-dark-30">
                  {{ item.binance_pay_id }}
                </span>
              </b-td>
              <b-td>
                <span class="mb-0 text-black-80 hp-text-color-dark-30">
                  {{ item.county_code || 'no country' }}
                </span>
              </b-td>
              <b-td>
                <span class="mb-0 text-black-80 hp-text-color-dark-30" v-if="item.already_claim === true"> ✅ </span>
                <span class="mb-0 text-black-80 hp-text-color-dark-30" v-else> ❌ </span>
              </b-td>
            </b-tr>
          </b-tbody>
        </b-table-simple>
        <ul role="menubar" aria-disabled="false" aria-label="Pagination" class="pagination b-pagination ml-5">
          <li role="presentation" aria-hidden="true" class="page-item">
            <span role="menuitem" aria-label="Go to previous page" aria-disabled="true" class="page-link" @click="prevPage">⏪</span>
          </li>
          <li role="presentation" aria-hidden="true" class="page-item mt-2">Page {{ userData.page }}</li>
          <li role="presentation" aria-hidden="true" class="page-item">
            <span role="menuitem" aria-label="Go to next page" aria-disabled="true" class="page-link" @click="nextPage">⏩</span>
          </li>
        </ul>
      </div>
    </div>
  </b-card>
</template>

<script>
import { BForm, BFormGroup, BAlert, BPagination, BFormInput, BLink, BFormSelect, BButton, BRow, BCol, BCard, BBadge, BTableSimple, BThead, BTbody, BTr, BTh, BTd } from 'bootstrap-vue';
import { mapActions, mapGetters, mapState } from 'vuex';

export default {
  computed: {
    ...mapGetters('binanceStore', ['userData']),
    filterStates() {
      return this.states.filter((item) => {
        return item.title.toLowerCase().includes(this.state.toLowerCase());
      });
    },
  },
  data() {
    return {
      selected: 'all',
      email: '',
      modal: false,
      newID: false,
      binance_id: 0,
      options: [
        { value: 'active', text: 'active' },
        { value: 'banned', text: 'banned' },
        { value: 'all', text: 'all' },
      ],
      errorfind: false,
    };
  },
  watch: {
    async selected(newValue, oldValue) {
      if (newValue === 'active') {
        await this.changeStateUser('active');
        await this.getListUser();
      }
      if (newValue === 'banned') {
        await this.changeStateUser('banned');
        await this.getListUser();
      }
      if (newValue === 'all') {
        await this.changeStateUser('all');
        await this.getListUser();
      }
    },
  },
  methods: {
    setState(state) {
      this.state = state;
      this.modal = false;
      this.$emit('searchClose');
    },
    ...mapActions('binanceStore', ['getListUser', 'findUsers', 'getPrevData', 'getNextData', 'newBinanceId', 'changePage']),
    async findUser() {
      const regex = /\d{5,}/;
      if (!regex.test(this.email)) {
        this.errorfind = true;
        return;
      }
      this.errorfind = false;
      await this.findUsers(this.email);
      console.log(this._email);
    },
    prevPage() {
      console.log('find user clicked');
      this.getPrevData();
    },
    nextPage() {
      console.log('find user clicked');
      this.getNextData();
    },
    async addId(_data) {
      console.log('user send data');
      await this.newBinanceId({
        binance_id: _data,
      });
      this.newID = false;
      await this.changePage(1);
      await this.getListUser();
    },
  },

  components: {
    BRow,
    BForm,
    BFormGroup,
    BCol,
    BCard,
    BBadge,
    BTableSimple,
    BThead,
    BTbody,
    BTr,
    BTh,
    BTd,
    BButton,
    BFormSelect,
    BFormInput,
    BLink,
    BPagination,
    BAlert,
  },
};
</script>
